/* hexagons */
/* &#x2B22; */
.port-wrap {
  position: fixed;
  top: 0;
  bottom: 110%;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.9);
  opacity: 0;
  z-index: 1000000; // this is getting out of hand - revisit!
  transition: bottom 0.4s ease-in-out;
  &.active {
    bottom: 0;
    opacity: 1;
  }
  > h2 {
    color: rgb(255, 255, 255);
    position: absolute;
    top: 1rem;
    right: 25rem;
    font-size: 3rem;
  }
  .close-the-pretty {
    position: fixed;
    top: 0;
    right: 2rem;
    padding: 0.5rem 1rem;
    background: rgb(255, 255, 255);
    border-radius: 0 0 5px 5px;
    font-weight: bold;
    z-index: 10;
  }
  .port-gallery {
    padding: 6rem 0 0 7rem;
    position: relative;
    height: 100%;

    .hex-outer {
      width: 142px;
      height: 80px;
      background-color: rgba(255, 255, 255, 0.5);
      position: relative;
      z-index: 1;
      float: left;
      text-align: center;
      margin: 44px 2px 0 2px;
      //transition: all .2s ease-in-out;

      &:nth-child(4n) {
        clear: left;
        margin: 44px 2px 0 -72px;
      }
      &:nth-child(8n) {
        clear: left;
        margin: 44px 2px 0 0;
      }
      &:before {
        content: "";
        width: 0;
        height: 0;
        border-bottom: 40px solid rgba(255, 255, 255, 0.5);
        border-left: 71px solid transparent;
        border-right: 71px solid transparent;
        position: absolute;
        top: -40px;
        left: 0;
      }
      &:after {
        content: "";
        width: 0;
        position: absolute;
        bottom: -40px;
        border-top: 40px solid rgba(255, 255, 255, 0.5);
        border-left: 71px solid transparent;
        border-right: 71px solid transparent;
        z-index: -1;
        left: 0;
      }
      .hex-inner {
        -webkit-clip-path: polygon(
          50% 0,
          100% 25%,
          100% 75%,
          50% 100%,
          0 75%,
          0 25%
        );
        clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
        background: #000;
        width: 132px;
        height: 150px;
        position: absolute;
        top: -35px;
        left: 5px;
        z-index: 2;
        margin: 0 auto;
        img {
          height: 200%;
          margin: -2% 0 0 -36%;
          transition: all 0.2s ease-in-out;
        }
      }
      .caption {
        position: fixed;
        left: 55%;
        color: rgb(255, 255, 255);
        top: 7rem;
        width: 36%;
        text-align: left;
        font-size: 1.5rem;
        line-height: 2rem;
        background: rgba(0, 0, 0, 0.5);
        z-index: 11;
        padding: 1rem;
        border: 1px solid rgba(255, 255, 255, 0.5);
        display: none;
      }
      a.mobile-hover-hack {
        z-index: 12;
        display: block;
        position: absolute;
        bottom: -20px;
        text-align: center;
        display: none;
        color: #fff;
        left: 6px;
        right: 6px;
        height: 14px;
        border-top: 1px solid rgb(0, 0, 0);
        padding-top: 8px;
        &:after {
          content: "";
          width: 0;
          position: absolute;
          bottom: -15px;
          border-top: 37px solid rgba(0, 0, 0, 0.7);
          border-left: 65px solid rgba(0, 0, 0, 0);
          border-right: 65px solid rgba(0, 0, 0, 0);
          z-index: -1;
          left: 0;
        }
      }
      &:hover {
        // transform: scale(1.5);
        z-index: 10;
        background-color: red; //rgba(136, 0, 0, 1);
        .caption {
          display: block;
        }
        img {
          transform: scale(1.5);
        }

        &:before {
          border-bottom: 40px solid red; //rgba(136, 0, 0, 1);
        }
        &:after {
          border-top: 40px solid red; //rgba(136, 0, 0, 1);
        }
        a.mobile-hover-hack {
          display: block;
        }
      }
    }
  }
}

@-webkit-keyframes pulsate {
  0% {
    -webkit-transform: scale(0.1, 0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2, 1.2);
    opacity: 0;
  }
}
