/* modal shade */
// lie unseen and silent
.modal-shade {
  display: none;
  opacity: 0;
}

// no body scroll when modal is up
body.modal {
  overflow: hidden;
}
.fancy-modal {
  position: fixed;
  top: -200%;
}
// technically more of a view than a modal currently, whatevs
body.modal {
  .modal-shade {
    content: "";
    text-align: right;
    font-size: 2em;
    position: fixed;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    left: 0;
    top: 0;
    min-height: 0;
    min-width: 0;
    z-index: 1000001;
    background-image: url(../img/zen-sharp-1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding: 86vh 0 0;
    color: rgba(255, 255, 255, 0.6);
    display: block;
    opacity: 1;
    -webkit-transition: height 0.5s linear;
    -moz-transition: height 0.5s linear;
    -ms-transition: height 0.5s linear;
    -o-transition: height 0.5s linear;
    transition: height 0.5s linear;
    &::after {
      content: "<breathe ... .. .> ";
      opacity: 0;
      font-size: 1.5em;
      position: fixed;
      height: 1.25em;
      width: auto;
      right: 2em;
      bottom: 0;
      min-height: 0;
      min-width: 0;
      z-index: 1000002;
      animation: breathe 15s infinite ease-in-out;
      animation-delay: 3s;
      animation-play-state: running;
      color: rgba(255, 255, 255, 0.6);
    }
    &.theme-1 {
      background-image: url(../img/zen-sharp-1.jpg);
    }
    &.theme-2 {
      background-image: url(../img/zen-sharp-2.jpg);
    }
    &.theme-3 {
      background-image: url(../img/zen-sharp-3.jpg);
    }
  }

  .fancy-modal {
    padding: 3em 1em 3.5em;
    z-index: 1000002;
    opacity: 0;
    background-image: url(../img/zen-blur-1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-image: linear-gradient(
        0deg,
        rgba(255, 255, 255, 0.4),
        rgba(255, 255, 255, 0.2)
      ),
      url(../img/zen-blur-2.jpg);
    width: 50vw;
    height: 30rem;
    left: 5vw;
    position: fixed;
    border-radius: 5px;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.3);
    text-align: left;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
    transition: box-shadow 0.3s ease;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.9);
    min-width: 0;
    min-height: 0;
    padding: 0;
    top: -200%;
    -webkit-transition: all 0.5s ease-out;
    -moz-transition: all 0.5s ease-out;
    -ms-transition: all 0.5s ease-out;
    -o-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
    .wrapper {
      position: relative;
    }
    .modal-content {
      overflow-x: hidden;
      overflow-y: auto;
      margin: 0 1em;
      padding: 0 2em;
      height: 18rem;
      background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.5) 5%,
        rgba(255, 255, 255, 0.5) 6%,
        rgba(255, 255, 255, 0.5) 94%,
        rgba(255, 255, 255, 0.5) 95%,
        rgba(255, 255, 255, 0) 100%
      );
    }
    h2 {
      margin: 0;
      top: -0.75em;
      position: absolute;
      font-size: 3em;
    }
    h5 {
      font-weight: 100;
      color: black;
      text-shadow: 0 1px 1px rgba(255, 255, 255, 0.8);
    }
    p {
      font-weight: 100;
      color: black;
      text-shadow: 0 1px 1px rgba(255, 255, 255, 0.8);
      margin: 0.5em 0 1em;
      &.important {
        padding: 1em;
        border: 1px dotted;
      }
      i:not(.fas) {
        font-weight: bold;
        border-bottom: 1px dotted;
      }
    }
    a.modal-closer {
      margin: 3em 1.5em 1em;
      padding: 0.5em 1em;
      background: rgba(131, 162, 131, 0.6);
      display: block;
      color: rgb(0, 0, 0);
      text-shadow: 0 1px 1px rgba(255, 255, 255, 0.4);
    }
    dl {
      color: rgb(0, 0, 0);
      text-shadow: 0 1px 1px rgba(255, 255, 255, 0.8);
      font-size: 1.25em;
      margin: 0 1em;
      dd {
        margin: 0;
        padding: 0.5em 1em 1em;
      }
    }

    &.theme-1 {
      background-image: url(../img/zen-blur-1.jpg);
      .modal-content {
        background: linear-gradient(
          90deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(0, 0, 0, 0.5) 5%,
          rgba(0, 0, 0, 0.5) 6%,
          rgba(0, 0, 0, 0.5) 94%,
          rgba(0, 0, 0, 0.5) 95%,
          rgba(255, 255, 255, 0) 100%
        );
      }
      p,
      dl {
        color: white;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.8);
      }
    }
    &.theme-2 {
      background-image: url(../img/zen-blur-2.jpg);
    }
    &.theme-3 {
      background-image: url(../img/zen-blur-3.jpg);
      .modal-content {
        background: linear-gradient(
          90deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(0, 0, 0, 0.5) 5%,
          rgba(0, 0, 0, 0.5) 6%,
          rgba(0, 0, 0, 0.5) 94%,
          rgba(0, 0, 0, 0.5) 95%,
          rgba(255, 255, 255, 0) 100%
        );
      }
      p,
      dl {
        color: white;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.8);
      }
      h2 {
        color: white;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.8);
      }
    }
    &.show {
      opacity: 1;
      top: 10%;
    }
    &.fancy {
      box-shadow: 0 3px 6px rgba(0, 0, 0, 0.25);
      border: 1px solid #bbb;
      -webkit-transform: translateY(-100%);
      -moz-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
      -o-transform: translateY(-100%);
      transform: translateY(-100%);
      &.show {
        -webkit-transform: translateY(0%);
        -moz-transform: translateY(0%);
        -ms-transform: translateY(0%);
        -o-transform: translateY(0%);
        transform: translateY(0%);
      }

      hr {
        margin: 0 auto;
        padding: 0;
        border: 0;
        height: 1px;
        text-align: center;
        background: rgb(255, 255, 255);
        background: linear-gradient(
          90deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(0, 0, 0, 1) 39%,
          rgba(0, 0, 0, 1) 40%,
          59%,
          rgba(0, 0, 0, 1) 60%,
          rgba(0, 0, 0, 1) 61%,
          rgba(255, 255, 255, 0) 100%
        );
        &::after {
          content: "\f192";
          display: inline-block;
          position: relative;
          top: 0.5em;
          padding: 0 1rem;
          font-family: FontAwesome;
          font-size: 1em;
        }
      }
      hr.above {
        margin: 4em auto 0;
        &::after {
          top: -1.5em;
        }
      }
      &.theme-3 {
        hr {
          &::after {
            color: #fff;
          }
        }
      }
    }
  }
}

@keyframes breathe {
  0% {
    transform: scale(1);
    opacity: 0.5;
  }
  20% {
    transform: scale(1.25);
    opacity: 1;
  }
  80% {
    transform: scale(1.25);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0.5;
  }
}

/* spiffy buttons */
.spiffy-btn {
  text-align: center;
  color: #fff;
  cursor: pointer;
  font-size: 0.8em;
  font-weight: 400;
  line-height: 1.5em;
  margin: 2em 1em 1em;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  width: auto;
  border: 1px solid;
  overflow: hidden;
  position: relative;
  border: 0 solid;
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
  outline: 1px solid;
  outline-color: rgba(255, 255, 255, 0.5);
  outline-offset: 0px;
  text-shadow: none;
  transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
  span {
    z-index: 20;
  }
  &:hover {
    border: 1px solid;
    box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.5),
      0 0 20px rgba(255, 255, 255, 0.2);
    outline-color: rgba(255, 255, 255, 0);
    outline-offset: 15px;
    //text-shadow: 1px 1px 2px #427388;
    text-decoration: none;
    &::after {
      left: 120%;
      transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    }
  }
  &::after {
    background: #fff;
    content: "";
    height: 155px;
    left: -75px;
    opacity: 0.3;
    position: absolute;
    top: -50px;
    transform: rotate(35deg);
    transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    width: 50px;
    z-index: -10;
  }
}

