/* pretty scrollbars */
.pretty-scroll {
  overflow-y: auto;
  overflow-x: hidden;
  &::-webkit-scrollbar {
    width: 12px;
  }
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.3);
  }
  &::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: rgba(#6b926b, 0.8);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
    &:window-inactive {
      background: rgba(255, 0, 0, 0.4);
    }
  }
}

/* social crap */
ul.share-buttons {
  list-style: none;
  padding: 0;
  margin: 1.5em 0 0;
  li {
    display: inline;
    padding: 0 0.5em;
    font-size: 2rem;
    a {
      opacity: 0.7;
      text-shadow: 1px 0px 0px white, -1px 0px 1px black;
      &:hover {
        text-decoration: none;
        opacity: 1;
      }
    }
    img {
      opacity: 0.7;
      height: 3em;
      &:hover {
        opacity: 0.1;
      }
    }
  }
  .sr-only {
    position: absolute;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0;
    border: 0;
    height: 1px;
    width: 1px;
    overflow: hidden;
  }
}
body.agile {
  ul.share-buttons {
    li {
      a {
        color: rgb(119, 103, 54);
      }
    }
  }
}

/* messy stack of paper */
body.agile .paper-stack {
  background: #fff;
  padding: 30px;
  position: relative;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25);
  border: 1px solid #bbb;
  &::before {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25);
    border: 1px solid #bbb;
    content: "";
    position: absolute;
    height: 95%;
    width: 99%;
    background-color: #eee;
    right: 15px;
    top: 0;
    transform: rotate(-1deg);
    z-index: -1;
  }
  &::after {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25);
    border: 1px solid #bbb;
    content: "";
    position: absolute;
    height: 95%;
    width: 99%;
    background-color: #eee;
    top: 5px;
    right: -5px;
    transform: rotate(1deg);
    z-index: -2;
  }
}

/* sketch borders */
body.agile {
  .sketchy {
    align-self: center;
    background: transparent;
    padding: 1rem 1rem;
    margin: 0 1rem;
    transition: all 0.5s ease;
    color: #41403e;
    letter-spacing: 1px;
    outline: none;
    box-shadow: 20px 38px 34px -26px hsla(0, 0%, 0%, 0.2);
    border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
    &:hover {
      box-shadow: 2px 8px 4px -6px hsla(0, 0%, 0%, 0.3);
    }
  }
  .sketchy-border {
    transition: all 0.5s ease;
    outline: none;
    box-shadow: 20px 38px 34px -26px hsla(0, 0%, 0%, 0.2);
    border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
    &:hover {
      box-shadow: 2px 8px 4px -6px hsla(0, 0%, 0%, 0.3);
    }
  }
  .lined.thick {
    border: solid 7px #41403e;
  }
  .dotted.thick {
    border: dotted 5px #41403e;
  }
  .dashed.thick {
    border: dashed 5px #41403e;
  }
  .lined.thin {
    border: solid 2px #41403e;
  }
  .dotted.thin {
    border: dotted 2px #41403e;
  }
  .dashed.thin {
    border: dashed 2px #41403e;
  }
}

/* post-its */
body.agile {
  .sticky {
    color: #000;
    display: inline-block;
    height: 24vw;
    width: 38vw;
    -moz-box-shadow: 5px 5px 7px rgba(33, 33, 33, 0.4);
    -webkit-box-shadow: 5px 5px 7px rgba(33, 33, 33, 0.4);
    box-shadow: 5px 5px 7px rgba(33, 33, 33, 0.4);
    -moz-transition: -moz-transform 0.15s linear;
    -o-transition: -o-transform 0.15s linear;
    -webkit-transition: -webkit-transform 0.15s linear;
    -webkit-transform: rotate(-4deg);
    -o-transform: rotate(-4deg);
    -moz-transform: rotate(-4deg);
    position: relative;
    top: -1em;
    left: 2em;
    background: #cfc;
    h4 {
      margin: 0;
      padding: 20px 20px 0;
    }
    &:hover {
      box-shadow: 10px 10px 7px rgba(0, 0, 0, 0.2);
      -moz-box-shadow: 10px 10px 7px rgba(0, 0, 0, 0.2);
      -webkit-box-shadow: 10px 10px 7px rgba(0, 0, 0, 0.2);
      -webkit-transform: scale(1.15);
      -moz-transform: scale(1.15);
      -o-transform: scale(1.15);
      position: relative;
      z-index: 5;
    }
    &:focus {
      box-shadow: 10px 10px 7px rgba(0, 0, 0, 0.2);
      -moz-box-shadow: 10px 10px 7px rgba(0, 0, 0, 0.2);
      -webkit-box-shadow: 10px 10px 7px rgba(0, 0, 0, 0.2);
      -webkit-transform: scale(1.15);
      -moz-transform: scale(1.15);
      -o-transform: scale(1.15);
      position: relative;
      z-index: 5;
    }
  }
}

/* index card */
body.agile {
  .note-card {
    background-image: linear-gradient(
      0deg,
      #c2466d 1.61%,
      transparent 1.61%,
      transparent 50%,
      transparent 50%,
      transparent 51.61%,
      transparent 51.61%,
      transparent 100%
    );
    background-size: 100px 80px;
    background-position: 0% -11%;
    background-repeat: repeat-x;
  }
  .note-card-text {
    background-image: linear-gradient(
      0deg,
      #8aaecc 3.85%,
      transparent 3.85%,
      transparent 50%,
      #8aaecc 50%,
      #8aaecc 53.85%,
      transparent 53.85%,
      transparent 100%
    );
    background-size: 52px 52px;
    background-position: left 25%;
    background-repeat: repeat;
    background-attachment: scroll;
    display: block;
    display: block;
    overflow: auto;
    height: 81%;
