}

/* pretty tacky */
body.agile {
  .add-tack {
    position: relative;
    &::before {
      content: "";
      display: block;
      background-color: #f7f7f7;
      background-image: -webkit-gradient(
        linear,
        left top,
        left bottom,
        from(#f7f7f7),
        to(#e7e7e7)
      );
      background-image: -webkit-linear-gradient(top, #f7f7f7, #e7e7e7);
      background-image: -moz-linear-gradient(top, #f7f7f7, #e7e7e7);
      background-image: -ms-linear-gradient(top, #f7f7f7, #e7e7e7);
      background-image: -o-linear-gradient(top, #f7f7f7, #e7e7e7);
      color: #a7a7a7;
      width: 24px;
      height: 24px;
      position: absolute;
      text-align: center;
      line-height: 144px;
      border-radius: 50%;
      box-shadow: 0px 3px 8px #aaa, inset 0px 2px 3px #fff;
      z-index: 50;
      left: 45%;
      top: 1.5em;
      font-family: $iconFont;
    }
    &::after {
      content: "";
      display: block;
      background: #fff;
      border-top: 2px solid #ddd;
      position: absolute;
      top: 1.5em;
      left: 45%;
      z-index: 49;
      border-radius: 50%;
      box-shadow: inset 0px 8px 48px #ddd;
      height: 40px;
      width: 40px;
      margin: -9px 0 0 -8px;
      font-family: $iconFont;
    }
  }
  .tack {
    position: relative;
    display: inline-block;
    margin-right: -4px;
    .inner {
      display: block;
      background-color: #f7f7f7;
      background-image: -webkit-gradient(
        linear,
        left top,
        left bottom,
        from(#f7f7f7),
        to(#e7e7e7)
      );
      background-image: -webkit-linear-gradient(top, #f7f7f7, #e7e7e7);
      background-image: -moz-linear-gradient(top, #f7f7f7, #e7e7e7);
      background-image: -ms-linear-gradient(top, #f7f7f7, #e7e7e7);
      background-image: -o-linear-gradient(top, #f7f7f7, #e7e7e7);
      color: #a7a7a7;
      margin: 13px;
      width: 24px;
      height: 24px;
      position: relative;
      text-align: center;
      line-height: 144px;
      border-radius: 50%;
      box-shadow: 0px 3px 8px #aaa, inset 0px 2px 3px #fff;
      float: left;
      &:after {
        content: "";
        display: block;
        background: #fff;
        border-top: 2px solid #ddd;
        position: absolute;
        top: -8px;
        left: -8px;
        bottom: -8px;
        right: -8px;
        z-index: -1;
        border-radius: 50%;
        box-shadow: inset 0px 8px 48px #ddd;
      }
    }
  }
  .note-card {
    .tack {
      .inner {
        position: absolute;
        left: 50%;
        top: 0;
        margin-left: -20px;
      }
      display: block;
    }
