/* bar chart (needs work) */
.barchart-wrapper {
  display: table;
  position: relative;
  margin: 0;
  height: 320px;
  font-size: 0.7em;
  .barchart-container {
    display: table-cell;
    width: 98%;
    height: 100%;
    padding-left: 15px;
    .barchart {
      display: table;
      table-layout: fixed;
      height: 100%;
      width: 100%;
      .barchart-col {
        position: relative;
        vertical-align: bottom;
        display: table-cell;
        height: 100%;
        z-index: 5;
        border-right: 0.5em solid transparent;
        .barchart-bar {
          position: relative;
          background: $chartBarColor;
          &:after {
            content: attr(data-prof);
            color: white;
            position: absolute;
            text-align: center;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            left: 50%;
            margin-left: -11px;
            top: 9px;
          }
        }
        .barchart-footer {
          position: absolute;
          white-space: nowrap;
          transform: rotate(-90deg);
          top: 95%;
          width: 0;
          margin-left: 48%;
          font-size: 1.25em;
        }
        .barchart-bar[data-prof="10"] {
          height: 100%;
        }
        .barchart-bar[data-prof="9"] {
          height: 90%;
        }
        .barchart-bar[data-prof="8"] {
          height: 80%;
        }
        .barchart-bar[data-prof="7"] {
          height: 70%;
        }
        .barchart-bar[data-prof="6"] {
          height: 60%;
        }
        .barchart-bar[data-prof="5"] {
          height: 50%;
        }
        .barchart-bar[data-prof="4"] {
          height: 40%;
        }
        .barchart-bar[data-prof="3"] {
          height: 30%;
        }
        .barchart-bar[data-prof="2"] {
          height: 20%;
        }
        .barchart-bar[data-prof="1"] {
          height: 10%;
        }
      }
    }
  }
  .barchart-prof-col {
    position: absolute;
    top: 0;
    width: 2%;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    .barchart-prof {
      height: 10%;
      vertical-align: middle;
      position: relative;
      .barchart-bar {
        &:first-child {
          .barchart-prof-text {
            top: -3px;
          }
        }
        &:last-child {
          .barchart-prof-text {
            top: -12px;
          }
        }
      }
      .barchart-prof-text {
        &:after {
          border-bottom: 1px dashed #ccc;
          content: "";
          position: absolute;
          width: 10000%;
          left: 0;
          bottom: -0.56em;
        }
        position: absolute;
        top: 0;
        z-index: 1;
        background: white;
        padding: 0.9em 0 0 0.5em;
        font-size: 1em;
      }
      &:last-child {
        &:after {
          border-bottom: none;
        }
      }
    }
  }
}

