/* tooltips */
h4.org-name a.tooltip,
div.gallery li.tooltip,
.share-buttons a.tooltip {
  position: relative;
}

section nav button span.tooltip,
footer span.tooltip {
  // special fellerz
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  display: block;
  z-index: 101;
}

.tooltip::after {
  content: attr(data-title);
  position: fixed;
  top: 0;
  left: 50%;
  display: block;
  padding: 0.25rem 0.5rem;
  border-radius: 2px;
  background: rgba(60, 50, 50, 0.7);
  color: #fff;
  font-size: 1rem;
  font-family: "Raleway", sans-serif;
  width: 300px;
  margin-left: -150px;
  z-index: 100;
  opacity: 0;
  text-align: center;
  cursor: pointer;
  line-height: 1rem;
  text-shadow: none;
  transform: unset;
  text-indent: 0;

  /* pre-transition */

  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
  margin: 0;
}
.share-buttons a.tooltip::after {
  content: attr(title);
}

#ResizR .tooltip::after,
footer span.tooltip::after {
  left: auto;
  top: 1em;
  right: 100%;
  position: absolute;
}

section nav button span.tooltip::after {
  left: auto;
  position: absolute;
  right: -5em;
}
h4.org-name a.tooltip::after,
div.gallery li.tooltip::after {
  left: auto;
  position: absolute;
}

.share-buttons a.tooltip::after {
  bottom: 5em;
  left: 24vw;
  top: auto;
}

/* visible state */
.tooltip:hover::after {
  height: auto !important;
  width: auto !important;
  overflow: auto !important;
  clip: auto !important;
  white-space: nowrap !important;
  text-indent: 0 !important;
  display: block !important;
}

.share-buttons a.tooltip:hover::after {
  width: 50vw !important;
  bottom: 6em;
  opacity: 1;
  -webkit-transition: opacity 0.4s ease-in;
  -moz-transition: opacity 0.4s ease-in;
  -ms-transition: opacity 0.4s ease-in;
  -o-transition: opacity 0.4s ease-in;
  transition: opacity 0.4s ease-in;
}
body header .tooltip::after {
  opacity: 0;
  top: 3em;
  width: 100%;
  left: 100%;
  position: absolute;
  -webkit-transition: opacity 0.4s ease-in-out, left 0.4s ease-in-out;
  -moz-transition: opacity 0.4s ease-in-out, left 0.4s ease-in-out;
  -ms-transition: opacity 0.4s ease-in-out, left 0.4s ease-in-out;
  -o-transition: opacity 0.4s ease-in-out, left 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out, left 0.4s ease-in-out;
}
body header .tooltip:hover::after,
body header .tooltip:focus::after {
  left: 0;
  opacity: 1;
}

#ResizR .tooltip:hover::after,
footer span.tooltip:hover::after {
  width: 10rem;
  right: 110%;
  opacity: 1;

  -webkit-transition: opacity 0.4s ease-in, right 0.4s ease-in;
  -moz-transition: opacity 0.4s ease-in, right 0.4s ease-in;
  -ms-transition: opacity 0.4s ease-in, right 0.4s ease-in;
  -o-transition: opacity 0.4s ease-in, right 0.4s ease-in;
  transition: opacity 0.4s ease-in, right 0.4s ease-in;
}
h4.org-name a.tooltip:hover::after,
div.gallery li.tooltip:hover::after,
section nav button span.tooltip:hover::after {
  height: 1rem;
