/* skewed zoomer */
body.agile .skewer {
  text-align: center;
  position: relative;
  li {
    transition: all 0.2s;
    display: inline-block;
    transition: transform(perspective 0.4s ease-in, rotateX 0.4s ease-in);
    position: relative;
    z-index: 5;
    &:hover {
      transform: perspective(800px) skew(1deg, 1deg) rotate(0deg) rotateY(0deg)
        rotateX(20deg) translate(0, 0) scale(1.5, 1.5) !important;
      transform-origin: center center;
      z-index: 10;
    }
    &:first-child {
      &:hover {
        transform: perspective(800px) rotate(-3deg) rotateY(2deg) rotateX(3deg)
          translate(0, 0) scale(1.5, 1.5) !important;
      }
    }
    &:last-child {
      &:hover {
        transform: perspective(800px) rotate(3deg) rotateY(2deg) rotateX(3deg)
          translate(0, 0) scale(1.5, 1.5) !important;
      }
    }
  }
  .add-tack {
    &:hover {
      &::before {
        background-color: none;
        background-image: none;
        width: 4px;
        height: 4px;
        line-height: 0;
        box-shadow: none;
        left: 48%;
        top: 0.6em;
        border: 1px solid #eee;
        background: #ceb288 url(../img/bg-cloth-bb.png);
      }
      &::after {
        display: none !important;
      }
    }
  }
}

