/* t'zoids TODO: make more clip paths */
body.agile {
  .poly-wrap {
    filter: drop-shadow(1px 2px 3px rgba(50, 50, 0, 0.5));
    color: black;
    position: relative;
    line-height: 1rem;
    background: none;
    box-shadow: none;
    &::after {
      content: attr(text);
      background: #fff url(../img/paper-texture.png);
      padding: calc(1em / 5);
      -webkit-clip-path: polygon(2% 0%, 99% 7%, 95% 95%, 3% 96%);
      clip-path: polygon(2% 0%, 99% 7%, 95% 95%, 3% 96%);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      height: 100%;
      width: 100%;
      margin: calc(1em / -5);
    }
  }
  .poly-wrap-b {
    filter: drop-shadow(1px 2px 3px rgba(50, 50, 0, 0.5));
    color: black;
    position: relative;
    line-height: 1rem;
    background: none;
    box-shadow: none;
    &::before {
      content: attr(text);
      background: #fff url(../img/paper-texture.png);
      padding: calc(1em / 5);
      -webkit-clip-path: polygon(20px 0%, 100% 10%, 95% 95%, 30px 100%);
      clip-path: polygon(20px 0%, 100% 10%, 95% 95%, 30px 100%);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      height: 100%;
      width: 100%;
      margin: calc(1em / -5);
    }
  }
  .poly-wrap.poly-0 {
    &::after {
      -webkit-clip-path: polygon(5% 2%, 95% 5%, 97% 99%, 0% 100%);
      clip-path: polygon(5% 2%, 95% 5%, 97% 99%, 0% 100%);
    }
  }
  .poly-wrap.poly-1 {
    &::after {
      -webkit-clip-path: polygon(4% 1%, 97% 1%, 98% 99%, 1% 96%);
      clip-path: polygon(4% 1%, 97% 1%, 98% 99%, 1% 96%);
    }
  }
  .poly-wrap.poly-2 {
    &::after {
      -webkit-clip-path: polygon(0% 0%, 97% 4%, 99% 98%, 2% 96%);
      clip-path: polygon(0% 0%, 97% 4%, 99% 98%, 2% 96%);
    }
  }
  .poly-wrap.poly-3 {
    &::after {
      -webkit-clip-path: polygon(2% 3%, 96% 3%, 100% 96%, 3% 97%);
      clip-path: polygon(2% 3%, 96% 3%, 100% 96%, 3% 97%);
    }
  }
  .poly-wrap.poly-4 {
    &::after {
      -webkit-clip-path: polygon(1% 4%, 99% 0%, 97% 100%, 4% 98%);
      clip-path: polygon(1% 4%, 99% 0%, 97% 100%, 4% 98%);
    }
  }
}

