.agile .swing {
  transform-origin: top center;
  animation: swing 4s ease;
}
@keyframes swing {
  20% {
    transform: rotate(15deg);
  }
  40% {
    transform: rotate(-10deg);
  }
  60% {
    transform: rotate(5deg);
  }
  80% {
    transform: rotate(-5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.agile .wobble {
  animation: wobble 8s ease;
}
@keyframes wobble {
  0% {
    transform: translateX(0%);
  }
  15% {
    transform: translateX(-25%) rotate(-5deg);
  }
  30% {
    transform: translateX(20%) rotate(3deg);
  }
  45% {
    transform: translateX(-15%) rotate(-3deg);
  }
  60% {
    transform: translateX(10%) rotate(2deg);
  }
  75% {
    transform: translateX(-5%) rotate(-1deg);
  }
  100% {
    transform: translateX(0%);
  }
}

/* Never has so much CSS been used for so little! */
.forg {
  color: rgb(76, 177, 76);
  animation-name: hop-2-it;
  animation-duration: 4s;
  animation-timing-function: ease-out;
  animation-delay: 3s;
  animation-iteration-count: infinite;
  animation-direction: linear;
  display: inline-block;
  animation-play-state: running;
  position: relative;
  text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  &:hover {
    animation-play-state: paused;
  }
}

@keyframes hop-2-it {
  0% {
    -webkit-transform: translate(0, 0) rotateY(0deg);
    transform: translate(0, 0) rotateY(0deg);
  }
  5% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  10% {
    -webkit-transform: translate(5px, -10px);
    transform: translate(5px, -10px);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  15% {
    -webkit-transform: translate(10px, 0);
    transform: translate(10px, 0);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  20% {
    -webkit-transform: translate(15px, -10px);
    transform: translate(15px, -10px);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  25% {
    -webkit-transform: translate(20px, 0);
    transform: translate(20px, 0);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  30% {
    -webkit-transform: translate(25px, -10px);
    transform: translate(25px, -10px);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  35% {
    -webkit-transform: translate(30px, 0);
    transform: translate(30px, 0);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  40% {
    -webkit-transform: translate(35px, -10px);
    transform: translate(35px, -10px);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  45% {
    -webkit-transform: translate(40px, 0);
    transform: translate(40px, 0);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  50% {
    -webkit-transform: translate(40px, 0) scale(-1, 1);
    transform: translate(40px, 0) scale(-1, 1);
  }
  55% {
    -webkit-transform: translate(35px, -10px) scale(-1, 1);
    transform: translate(35px, -10px) scale(-1, 1);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  60% {
    -webkit-transform: translate(30px, 0) scale(-1, 1);
    transform: translate(30px, 0) scale(-1, 1);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  65% {
    -webkit-transform: translate(25px, -10px) scale(-1, 1);
    transform: translate(25px, -10px) scale(-1, 1);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  70% {
    -webkit-transform: translate(20px, 0) scale(-1, 1);
    transform: translate(20px, 0) scale(-1, 1);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  75% {
    -webkit-transform: translate(15px, -10px) scale(-1, 1);
    transform: translate(15px, -10px) scale(-1, 1);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  80% {
    -webkit-transform: translate(10px, 0) scale(-1, 1);
    transform: translate(10px, 0) scale(-1, 1);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  85% {
    -webkit-transform: translate(5px, -10px) scale(-1, 1);
    transform: translate(5px, -10px) scale(-1, 1);
    text-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
  }
  90% {
    -webkit-transform: translate(0, 0) scale(-1, 1);
    transform: translate(0, 0) scale(-1, 1);
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  }
  95% {
    -webkit-transform: translate(0, 0) scale(-1, 1);
    transform: translate(0, 0) scale(-1, 1);
  }
  100% {
    -webkit-transform: translate(0, 0) scale(-1, 1);
    transform: translate(0, 0) scale(-1, 1);
  }
} // Sweet baby jebus!

